export default {
	data() {
		return {
			imgList: [],
			merInfo: {}
		};
	},
	onLoad() {
		let mid = this.getMerchantInfo().id;
		if(mid){
			uni.getStorage({
				key: "LOGIN_MERCHANT",
				success: (res) => {
					this.merInfo = res.data;
					this.imgList.push(this.merInfo.avatarUrl)
				}
			})
		}else{
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
			setTimeout(()=>{
				uni.switchTab({
					url:"../tabbar/user"
				})
			},1000)
		}
	},
	methods: {
		ChooseImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					let file = res.tempFilePaths;
					this.imgList = file;
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '确认删除吗',
				cancelText: '再看看',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		submit() {
			uni.showLoading({
				title: "提交中"
			})
			console.log(this.imgList[0].indexOf('tmp'))
			if (this.imgList[0].indexOf('tmp') < 0) {
				uni.request({
					url: this.prefix + "/doEditInfo",
					data: {
						id: this.merInfo.id,
						mname: this.merInfo.mname,
						mphone: this.merInfo.mphone,
						mdesc: this.merInfo.mdesc
					},
					success: (res) => {
						if (res.data.code === 200) {
							setTimeout(() => {
								uni.hideLoading();
								uni.showToast({
									title: "修改成功",
									icon: "success"
								});
							}, 800)
							setTimeout(() => {
								uni.setStorageSync("LOGIN_MERCHANT", res.data.data)
								uni.reLaunch({
									url: "../tabbar/user"
								})
							}, 1600)
						} else {
							uni.showToast({
								title: "修改信息异常",
								icon: "none"
							})
						}
					}
				})
			} else {
				uni.uploadFile({
					url: this.prefix + "/doEditInfo",
					filePath: this.imgList[0],
					name: 'file',
					header: {
						"Content-Type": "multipart/form-data"
					},
					formData: {
						id: this.merInfo.id,
						mname: this.merInfo.mname,
						mphone: this.merInfo.mphone,
						mdesc: this.merInfo.mdesc
					},
					success: (res) => {
						if (res.data.code === 200) {
							setTimeout(() => {
								uni.hideLoading();
								uni.showToast({
									title: "修改成功",
									icon: "success"
								});
							}, 800)
							setTimeout(() => {
								uni.setStorageSync("LOGIN_MERCHANT", res.data.data)
								uni.reLaunch({
									url: "../tabbar/user"
								})
							}, 1600)
						} else {
							uni.showToast({
								title: "修改信息异常",
								icon: "none"
							})
						}
					}
				})
			}

		}
	}
}
